
#app {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #32485F;
  font-size: 16px;
  max-width: 1025px;
  margin: 0 auto;
}

.configurator {
  margin-bottom: 15px;
}

.question {
  text-align: left;
  font-size: 14px;
}

.answers {
  margin-top: 10px;
}
.answer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px 10px;
  font-family: Consolas,Monaco,'Andale Mono','Ubuntu Mono',monospace;
  min-width: 100%;
  max-width: 100%;
  min-height: 100px;
  text-align: left;
  font-size: 14px;
}

.paginator {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.paginator__item {
  margin-bottom: 0.5em;
  padding: 0.5em 2em;
  border-radius: 7px;
  background-color: lightblue;
  color: #32485F;
}
.paginator__item:hover {
    background-color: #82cde6;
    cursor: pointer;
}
